:host {
  display: block;
  background: var(--tp-color-neutral-1);
  color: var(--tp-color-neutral-10);
}

.tp-calendar {
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 24px;
    background-color: transparent;
  }

  &__header-left {
    display: flex;
    align-items: center;
    text-align: center;
    width: 210px;
  }

  &__label {
    flex: 1;
  }

  &__header-right {
    display: flex;
    align-items: center;
  }

  &__icon {
    color: var(--tp-color-neutral-8);
  }

  &__content-header {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    text-align: center;
  }

  &__week-day {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 24px;
    margin: 0 auto;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    height: 100%;
  }
}

//
// color
//
:host(.tp-color) {
  ::slotted(tp-calendar-day) {
    --tp-active-color: var(--tp-color-contrast);
    --tp-active-background: var(--tp-color-base);
  }
}
