import {
  Component,
  Event,
  EventEmitter,
  h,
  Host,
  Prop,
  State,
} from '@stencil/core';
import { createColorClasses } from '../../../utils/functions/color.function';
import { TpColor } from '../../../utils/types/color.type';
import { TP_CALENDAR } from './utils/calendar.constant';
import {
  TpCalendarChangeDirection,
  TpCalendarTimeframe,
} from './utils/calendar.type';
import { createGesture } from './utils/gesture';
import { Gesture, GestureConfig } from './utils/gesture/index';

@Component({
  tag: 'tp-calendar',
  styleUrl: 'calendar.scss',
  assetsDirs: ['assets'],
  scoped: true,
})
export class TpCalendar {
  /**
   * Define a variaÃ§Ã£o de cor do componente.
   */
  @Prop({ reflect: true }) color: TpColor;

  /**
   * Define a etiqueta do conteÃºdo sendo exibido.
   */
  @Prop({ reflect: true }) label: string;

  /**
   * Define o valor inicial do intervalo de tempo que representa
   * o conteÃºdo sendo exibido.
   */
  @Prop({ reflect: true }) initialTimeframe: TpCalendarTimeframe =
    TP_CALENDAR.timeframes.week;

  /**
   * Define o valor atual do intervalo de tempo que representa
   * o conteÃºdo sendo exibido.
   */
  @State() timeframe: TpCalendarTimeframe;

  /**
   * Evento emitido quando o conteÃºdo deve avanÃ§ar ou retroceder
   * por requisiÃ§Ã£o do usuÃ¡rio
   */
  @Event() tpCalendarChange: EventEmitter;

  /**
   * Evento emitido quando o intervalo de tempo exibido deve mudar
   * por requisiÃ§Ã£o do usuÃ¡rio
   */
  @Event() tpTimeframeToggle: EventEmitter;

  /**
   * Objeto que representa as configuraÃ§Ãµes de gestos (eventos customizados).
   */
  gesture: Gesture;

  /**
   * O container do conteÃºdo sendo exibido.
   */
  container: HTMLDivElement;

  /**
   * MÃ©todos internos
   */
  onTimeframeToggle() {
    const currentTimeframe = this.timeframe ?? this.initialTimeframe;
    this.timeframe =
      currentTimeframe === TP_CALENDAR.timeframes.week
        ? TP_CALENDAR.timeframes.month
        : TP_CALENDAR.timeframes.week;
    this.tpTimeframeToggle.emit(this.timeframe);
  }

  onCalendarChange(direction: TpCalendarChangeDirection) {
    this.tpCalendarChange.emit(direction);
  }

  setupGesture() {
    let direction: TpCalendarChangeDirection;

    const options: GestureConfig = {
      el: this.container,
      gestureName: 'swipe',
      onStart: () => {},
      onMove: (event) => {
        if (event.deltaX > 0) {
          direction = TP_CALENDAR.directions.left;
        } else {
          direction = TP_CALENDAR.directions.right;
        }
      },
      onEnd: () => {
        this.tpCalendarChange.emit(direction);
      },
    };

    this.gesture = createGesture(options);
    this.gesture.enable();
  }

  disconnectGesture() {
    if (this.gesture) {
      this.gesture.destroy();
    }
  }

  /**
   * Lifecycle hooks.
   */
  componentDidLoad() {
    this.setupGesture();
  }

  disconnectedCallback() {
    this.disconnectGesture();
  }

  render() {
    const { color, label, initialTimeframe, timeframe } = this;

    return (
      <Host
        class={createColorClasses(color, {
          'tp-calendar': true,
        })}
      >
        <div class="tp-calendar__header">
          <div class="tp-calendar__header-left">
            <ion-button
              // @ts-ignore
              size="xxs"
              fill="clear"
              onClick={() => this.onCalendarChange(TP_CALENDAR.directions.left)}
            >
              <ion-icon
                slot="icon-only"
                src={TP_CALENDAR.icons.chevronLeft}
              ></ion-icon>
            </ion-button>

            <ion-text class="tp-calendar__label" tp-type="p16b">
              {label}
            </ion-text>

            <ion-button
              // @ts-ignore
              size="xxs"
              fill="clear"
              onClick={() =>
                this.onCalendarChange(TP_CALENDAR.directions.right)
              }
            >
              <ion-icon
                slot="icon-only"
                src={TP_CALENDAR.icons.chevronRight}
              ></ion-icon>
            </ion-button>
          </div>

          <div class="tp-calendar__header-right">
            <ion-button
              // @ts-ignore
              size="xxs"
              fill="clear"
              onClick={() => this.onTimeframeToggle()}
            >
              {timeframe ?? initialTimeframe}

              <ion-icon
                class="tp-calendar__icon"
                slot="end"
                src={TP_CALENDAR.icons.chevronDown}
              ></ion-icon>
            </ion-button>
          </div>
        </div>

        <div class="tp-calendar__content">
          <div class="tp-calendar__content-header">
            {TP_CALENDAR.weekdays.map((day) => (
              <div class="tp-calendar__week-day">
                <ion-text tp-type="p12" color="neutral-8">
                  {day}
                </ion-text>
              </div>
            ))}
          </div>
          <div
            class="tp-calendar__container"
            ref={(el) => (this.container = el)}
          >
            <slot></slot>
          </div>
        </div>
      </Host>
    );
  }
}
